body {
  padding: 0;
  font-family: 'Montserrat', sans-serif !important;
}

h3 {
  font-family: 'Alex Brush', cursive;
  font-size: 48px;
  font-weight: bold;
  color: #958653;
}

.hero {
  color: #fff;
  // background: url(../../assets/img/img0-1.jpg) center #f7f5f1 no-repeat;
  background: url(../../assets/img/img11.jpg) center center #f7f5f1 no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  // min-height: 500px;
  text-align: center;


  h1 {
    font-size: 90px;
    font-family: 'Alex Brush', cursive;
    color: #fff;
  }

  h2 {
    font-size: 60px;
    font-family: 'Alex Brush', cursive;
    color: #fff;
  }

  p {
    font-size: 18px;
    font-style: italic;
  }

  span {
    font-style: italic;
    font-size: 65px;
    color: #fff;
  }
}

.cuenta {
  text-align: center;
  font-family: sans-serif;

  p {
    font-size: 130px;
    margin: 0px;
  }

  span {
    font-size: 20px;
    font-weight: bold;
  }

  .time {
    position: relative;
  }

  .days {
    position: absolute;
    top: -45px;
    left: 350px;
  }

  .hrs {
    position: absolute;
    top: -45px;
  }

  .min {
    position: absolute;
    top: -45px;
    right: 300px;
  }
}

.detalles {
  text-align: center;
  background-color: #f7f5f1;

  span {
    font-size: 50px;
  }

  button {
    border-radius: 5px;
    padding: 5px 10px;
    background: transparent;
    font-size: 16px;
    border: #000 2px solid;
    color: #958653 !important;
  }

  h2 {
    font-family: 'Alex Brush', cursive;
    font-size: 48px;
    font-weight: bold;
    color: #958653;
  }
}

